<!--留言组件-->
<template>
  <div>
    <!-- banner -->
    <div class="message-banner">
      <!-- 弹幕输入框 -->
      <div class="message-container">
        <h1 class="message-title">留言板</h1>
        <div class="animated fadeInUp message-input-wrapper">
          <input
            v-model="from.messageContent"
            @click="show = true"
            placeholder="说点什么吧" style="background: transparent;"
          />
          <button
            class="ml-3 animated bounceInLeft"
            @click="addToList"
            v-show="show"
          >
            发送
          </button>
        </div>
      </div>
      <!-- 弹幕列表 -->
      <div class="barrage-container">
        <vue-baberrage
            :barrageList="barrageList"
            :loop="true">
          <template v-slot:default="slotProps">
            <span class="barrage-items">
              <img
                :src="slotProps.item.avatar"
                width="30"
                height="30"
                style="border-radius:50%"
              />
              <span class="ml-2">{{ slotProps.item.nickname }} :</span>
              <span class="ml-2">{{ slotProps.item.messageContent }}</span>
            </span>
          </template>
        </vue-baberrage>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  mounted () {
    this.listMessage()
  },
  data () {
    return {
      from:{
        userId:'',//用户id
        messageContent:'',//用户留言内容
      },
      show: false,
      barrageList: [],//留言数据
    }
  },
  methods: {
    //点击发送留言方法
    async addToList () {
      if(JSON.parse(sessionStorage.getItem("user"))){
        if(this.from.messageContent!==''){
          // 发送留言
          //获取用户id
          this.from.userId=JSON.parse(sessionStorage.getItem("user")).id
          this.$http.post('/message/saveMessage',this.from).then(res => {
            if(res.data.result){
              this.listMessage()
              this.$message({
                message: '留言成功',
                type: 'success',
                offset:80
              });
              this.from.messageContent='';
            }
          })
        }else {
          this.$message({
            message: '留言不能为空',
            type: 'warning',
            offset:80
          });
        }
      }else {
        this.$message({
          message: '请登录后进行操作',
          type: 'warning',
          offset:80
        });
        this.$router.push({name:'login'})
      }
    },
    //获取留言信息
    async listMessage () {
      const { data: res } = await this.$http.get('/message/selectMessageShow')
      if (res.result) {
        this.barrageList = res.data.messageList
      }
    },
  }
}
</script>

